/* eslint import/no-webpack-loader-syntax: off */
import React, { useState } from 'react';
import LogoImage from '-!react-svg-loader!../../images/LanistaLogo.svg';
import {
  Root,
  LanistaLogo,
  StyledLink,
  EmailValidationMessage,
  PasswordValidationMessage,
  StyledLoginButton,
  StyledRegisterButton,
  Footer,
  Nav
} from './styles';
import LanistaButton from '../../components/LanistaButton';
import LanistaTextField from '../../components/LanistaTextField';
import { Menu, MainButton, ChildButton } from "react-mfb";
import {useSpring, animated} from 'react-spring';
import Link from 'next/link';
import TrainerList from './TrainerList';

const effect = 'zoomin', pos = 'br', method = 'hover';

const MyFooter = ({currentLanguage, languageItems}) => {
  //const [visible, toggle] = useState(true)
  const props = useSpring({opacity: 1, from:{opacity: 0}})
  return (
    <animated.div style={props}>
      <Footer>
        <Nav style={{color: 'black'}}>
          © Lanista Trainingssoftware 2012
          <a>
            Impresum
          </a>
          <a>
            Datenschutz
          </a>
          <a>
            Info
          </a>
        </Nav>
        <Menu effect={effect} method={method} position={pos}>
          <MainButton className={currentLanguage + "-flag"} iconResting="ion-plus-round" iconActive="ion-close-round" />
          {languageItems}
        </Menu>
      </Footer>
    </animated.div>
  )
}

// ----------------------------------------------------------------------------

// Say hello from GraphQL, along with a HackerNews feed fetched by GraphQL
class Login extends React.Component {

  constructor(props) {
    super(props);
    // Don't call this.setState() here!
    this.state = { show: false };
  }

  componentDidMount() {
    const _this = this
    setTimeout(function(){
      _this.setState({
        show: true
      });
    }, 2000);
  }

  componentWillUnmount() {
    this.setState({
      show: false
    });
  }

  render() {

    const {show} = this.state;
    const {
      authenticateUser,
      authenticating,
      goToRegistration,
      goToForgotpassword,
      error,
      errorMessage,
      t,
      languages,
      currentLanguage,
      onChangeLanguage,

      email,
      handleEmailChange,
      emailIsValid,

      password,
      handlePasswordChange,
      passwordIsValid,

      validationEmailErrorMessage,
      validationPasswordErrorMessage,
      authenticationErrorMessage,

      tbt,
    } = this.props;

    const languageItems = (languages ? languages.map((language) => <ChildButton
      icon="ion-social-github"
      label={language}
      className={language + "-flag"}
      key={language}
      onClick={(e) => {
        onChangeLanguage(language);
      }} />)
      : <ChildButton/>);

    return (
      <Root className={"scene"}>
        <div className="main-section">
          {
            tbt && (
              <TrainerList
                t={t}
                tbt={tbt}

                password= {password}
                onPasswordChange={handlePasswordChange}

                onEmaiChange={handleEmailChange}
                disabled={authenticating}
                error={passwordIsValid}
                helperText={validationPasswordErrorMessage || authenticationErrorMessage}

                authenticateUser={authenticateUser}
                loading={authenticating}

                goToForgotpassword={goToForgotpassword}
              />
            )
          }
          { !tbt && (
            <div className="form-section">

              <LanistaLogo  style={{}}>
                <LogoImage width={60} height={60}/>
                <div className="sub-header">
                  Lanista<span>Coach</span>
                </div>
              </LanistaLogo>

              <div className="input-fields-section">
                <LanistaTextField
                  className="email-field"
                  variant="outlined"
                  placeholder='Email'
                  disabled={authenticating}
                  type={"email"}
                  error={emailIsValid}
                  value= {email}
                  onChange={handleEmailChange}
                  helperText="Incorrect entry."
                  helperText={validationEmailErrorMessage}
                />
                <LanistaTextField
                  className="password-field"
                  variant="outlined"
                  placeholder='Password'
                  disabled={authenticating}
                  type={"password"}
                  error={passwordIsValid}
                  value= {password}
                  onChange= {handlePasswordChange}
                  helperText={validationPasswordErrorMessage || authenticationErrorMessage}
                />
              </div>


              <StyledLink onClick={goToForgotpassword}>
                  <a >{t("login:forgot_password")}</a>
              </StyledLink>

              <div className="buttons-section">
                <LanistaButton
                  loading={authenticating}
                  onClick={
                    () => {
                      authenticateUser();
                    }
                  }
                  disabled={authenticating}
                  inverted
                >
                  { authenticating ? ("...") : t("login") }
                </LanistaButton>
                <LanistaButton
                  onClick={
                    (e) => {
                      const {x, y, height, width} = e.target.getBoundingClientRect();
                      goToRegistration({
                        x,
                        y,
                        height,
                        width
                      });
                      //this.setState({show: false});
                    }
                  }
                disabled={authenticating}
                >
                  {authenticating ? ("...") : t("register")}
                </LanistaButton>
              </div>
            </div>
          )}

        </div>
        <MyFooter currentLanguage={currentLanguage} languageItems={languageItems}/>
      </Root>
    )
  }
};

export default Login;
